<template>
  <!--  -->
  <div class="first1">
    <div class="list" v-for="(l,i) in lists" :key="i">
      <div class="title" @click="toggle(i)">{{l.title}}</div>
      <ul class="item" v-show="l.show">
        <li v-for="(item,index) in l.items" :key="index">{{item}}</li>
      </ul>
    </div>
    <div>
      <p class="pp"></p>
    </div>

  </div>
</template>

<script>
export default {
  data() {
    return {
      lists: [
        {
          title: "",
          items: ["D", ""],
          show: false
        },
        {
          title: "",
          items: ["D", "", "", ""],
          show: true
        },
        {
          title: "",
          items: [" ", " ", " "],
          show: false
        },
        {
          title: "",
          items: [
            "",
            "",
            "",
            "",
            "",
            "",
            ""
          ],
          show: false
        },
        {
          title: "",
          items: [
            "D",
            "",
            "",
            "",
            "",
            "",
            "",
            "",
            "",
            ""
          ],
          show: false
        }
      ]
    };
  },
  methods: {
    toggle(index) {
      // ES6 
      // return
      this.lists.forEach((list, i) => (list.show = i == index));
      //  list,i
      // arr 
      this.lists.forEach(function(list, i, arr) {
        //     // console.log(arr[i]==list);
        return (list.show = i == index);
        if (i == index) {
          list.show = true;
        } else {
          list.show = false;
        }
      });
    }
  }
};
</script>

<!--css-->
<style scoped>
.pp {
  color: #aaa;
  font-size: 1.5rem;
  box-shadow: 0px 6px 9px 12px #f00;
  display: inline-block;
  margin-top: 1rem;
}

ul {
  list-style: none;
  transition: all 1s linear;
}

li {
  padding-left: 10px;
  line-height: 2;
}

li:hover {
  background-color: #eee;
  color: #0f0;
}

.list {
  padding: 0 50px;
  background: rgb(45, 4, 46);
}

.list .title {
  border: 1px solid #dfa;
  padding: 10px 5px;
  cursor: pointer;
  -webkit-user-select: none;
  user-select: none;
  font-size: 18px;
  font-weight: bold;
  color: #0ff;
  box-shadow: 4px 5px 11px 5px #000;
}

.list .item {
  box-shadow: 4px 5px 11px 5px #fff;
}
.item > li {
  color: #f00;
}
.item > li:hover {
  background-color: #aa0;
  cursor: pointer;
}
</style>